	export default {
		data() {
			return {
				city: '上海',
                resultFlag: false,
                result: {
                    forecast:[]
                }
			}
		},
		methods: {
            getValue(event){
                this.city = event.target.value
            },
			search(){
                console.log(this.city)
                uni.showLoading({
                    title: '正在搜索...'
                })
                uni.request({
                    url: 'https://www.apiopen.top/weatherApi',
                    data: {
                        city: this.city,
                        // key: '838020829af6b7ff274971866201c3c5'
                    },
                    success: (res) => {
                        uni.hideLoading()
                        if(res.data.code===200){
                            this.result = res.data.data
                            this.resultFlag = true
                        }
                            
                    }
                })
            }
		}
	}
